import Cookies from 'js-cookie';
import { useMutation, useQueryClient } from 'react-query';
import { getConfig } from '../utilities/platformServices';
var createSatelliteManifest = function (data) {
    var name = data.name, version = data.version;
    var jwtToken = Cookies.get('cs_jwt');
    var rhsmAPIBase = getConfig().rhsmAPIBase;
    return fetch(rhsmAPIBase + "/management/v1/allocations?name=" + name + "&version=" + version, {
        method: 'POST',
        headers: { Authorization: "Bearer " + jwtToken }
    })
        .then(function (response) {
        if (response.status !== 200) {
            throw new Error("Status Code " + response.status + ".  Error creating manifest: " + response.statusText + ".  ");
        }
        return response.json();
    })
        .catch(function (e) {
        console.error(e);
    });
};
var useCreateSatelliteManifest = function () {
    var queryClient = useQueryClient();
    return useMutation(function (newManifest) { return createSatelliteManifest(newManifest); }, {
        onSuccess: function () {
            queryClient.invalidateQueries('manifests');
        }
    });
};
export { createSatelliteManifest, useCreateSatelliteManifest as default };
