import { useQuery } from 'react-query';
import Cookies from 'js-cookie';
import { getConfig } from '../utilities/platformServices';
var fetchSatelliteVersions = function () {
    var jwtToken = Cookies.get('cs_jwt');
    var rhsmAPIBase = getConfig().rhsmAPIBase;
    return fetch(rhsmAPIBase + "/management/v1/allocations/versions", {
        headers: { Authorization: "Bearer " + jwtToken },
        mode: 'cors'
    })
        .then(function (response) { return response.json(); })
        .catch(function (e) {
        console.error('Error fetching Satellite Versions', e);
    });
};
var useSatelliteVersions = function () {
    return useQuery('satelliteVersions', function () { return fetchSatelliteVersions(); });
};
export { fetchSatelliteVersions, useSatelliteVersions as default };
